<template>
    <div class="container">
        <div class="loading" :style="isLoading">
            <div class="lds-ripple">
                <div></div>
                <div></div>
            </div>
        </div>
        <div class="row">
            <div class="col-6 offset-3 pt-3 card mt-5 shadow">
                <div class="card-body">
                    <h3>rn lemleri</h3>
                    <hr>
                    <div class="form-group">
                        <label>rn Ad</label>
                        <input v-model="product.title" type="text" class="form-control"
                            placeholder="rn adn giriniz..">
                    </div>
                    <div class="form-group">
                        <label>Adet</label>
                        <input v-model="product.count" type="number" class="form-control"
                            placeholder="rn adetini giriniz..">
                    </div>
                    <div class="form-group">
                        <label>Fiyat</label>
                        <input v-model="product.price" type="number" class="form-control"
                            placeholder="rn fiyat giriniz..">
                    </div>
                    <div class="form-group">
                        <label>Aklama</label>
                        <textarea v-model="product.description" cols="30" rows="5"
                            placeholder="rne ait bir aklama giriniz..." class="form-control"></textarea>
                    </div>
                    <hr>
                    <button class="btn btn-primary" :disabled="saveEnebled" @click="saveProduct">Kaydet</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            product: {
                title: "",
                count: "null",
                price: "null",
                description: "",
            },
            saveButtonClicked: false
        }
    },
    methods: {
        saveProduct() {
            this.saveButtonClicked = true;
            this.$store.dispatch("saveProduct", this.product)
        }
    },
    computed: {
        saveEnebled() {
            if (this.product.title.length > 0 && this.product.count > 0 && this.product.price > 0 && this.product.description.length > 0) {
                return false;
            } else {
                return true;
            }
        },
        isLoading() {
            if (this.saveButtonClicked) {
                return {
                    display: "block"
                }
            } else {
                return {
                    display: "none"
                }
            }
        }
    },
    beforeRouteLeave(to, from, next) {

        if ((this.product.title.length > 0 || this.product.count > 0 || this.product.price > 0 || this.product.description.length > 0) && !this.saveButtonClicked) {
            if (confirm("Kaydedilmemi deiiklikler var, yine de kmak istiyor musunuz?")) {
                next();
            } else {
                next(false);
            }
        } else {
            next();
        }
    }
}
</script>

<style>
</style>


